import React, { useState } from "react";
import Card from "../UI/Card";
import styles from "./UserInput.module.css";
import Button from "../UI/Button";

function UserInput(props) {
  const [data, setData] = useState({
    username: "",
    age: "",
  });

  function changeHandler(event) {
    const { name, value } = event.target;
    setData((prevValue) => {
      return { ...prevValue, [name]: value };
    });
  }

  function submitHandler(event, props) {
    event.preventDefault();
    props.addUser({ ...data, id: Math.random() });
    setData({
      username: "",
      age: "",
    });
  }

  return (
    <Card>
      <form onSubmit={(event) => submitHandler(event, props)}>
        <label htmlFor="Username">
          <h4 className={styles["form-label"]}>Username</h4>
        </label>
        <input
          value={data.username}
          onChange={changeHandler}
          name="username"
          type="text"
          id="Username"
          className={styles["form-input"]}
        />
        <label htmlFor="Age">
          <h4 className={styles["form-label"]}>Age</h4>
        </label>
        <input
          value={data.age}
          onChange={changeHandler}
          name="age"
          type="text"
          id="Age"
          className={styles["form-input"]}
        />
        <Button className={styles["form-submit"]}>Submit</Button>
      </form>
    </Card>
  );
}

export default UserInput;
